<template>
  <div class="searchview">
    <van-sticky>
      <div class="top">
        <router-link tag="div" to="/searchmsg" class="topSearch">
          <span class="iconfont icon-sousuoxiao"></span>
          <p>用达芬奇拯救周董发福的脸颊</p>
        </router-link>
      </div>
    </van-sticky>

    <div class="loadingView" v-if="!banners">
      <div class="loading"></div>
      <van-loading type="spinner" size="20" vertical />
    </div>
    <div v-else>
      <div class="banner" v-if="banners">
        <van-swipe @change="onChange" autoplay="3000">
          <van-swipe-item v-for="item in banners" :key="item.id">
            <img :src="item.cover" alt="" />
            <div class="describe">{{ item.title }}</div>
          </van-swipe-item>
          <template #indicator>
            <div class="custom-indicator">
              <span>{{ current + 1 }}</span> / {{ banners.length }}
            </div>
          </template>
        </van-swipe>
      </div>
      <div class="recommendedPosition">
        <div
          class="block"
          v-for="item in recommended_position"
          :key="item.icon"
        >
          <div class="imgView">
            <img :src="item.icon" alt="" />
          </div>
          <p>{{ item.title }}</p>
        </div>
      </div>
      <div class="worksView">
        <h3>作品</h3>
        <ul>
          <router-link tag="li" :to="'categoryview?cid='+item.id" v-for="item in categories" :key="item.id">
            <img :src="item.cover" alt="" />
            <p>{{ item.category_name }}</p>
          </router-link>
        </ul>
      </div>
    </div>
    <tab-bar :tab-index="tabIndex"></tab-bar>
  </div>
</template>

<script>
import TabBar from "../components/public/TabBar.vue";
import { getPageDiscovery } from "../api/search";
export default {
  data() {
    return {
      tabIndex: 1,
      current: 0,
      banners: null,
      recommended_position: null,
      categories: null,
    };
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    getPageDiscoveryFun() {
      getPageDiscovery().then((data) => {
        // console.log(data);
        this.banners = data.data.banners;
        this.recommended_position = data.data.recommended_position;
        this.categories = data.data.categories;
      });
    },
  },
  created() {
    this.getPageDiscoveryFun();
  },
  components: {
    TabBar,
  },
};
</script>

<style lang="less">
.searchview {
  width: 100%;
  height: 100%;
  .top {
    width: 100%;
    height: 42px;
    background-color: #fff;
    border-bottom: 1px solid #e8e8e8;
    display: flex;
    align-items: center;
    .topSearch {
      width: 93.6%;
      height: 30px;
      background-color: #f0f0f0;
      margin: 0 auto;
      border-radius: 3px;
      display: flex;
      align-items: center;
      .iconfont {
        font-size: 14px;
        color: #878787;
        margin-left: 14px;
      }
      p {
        font-size: 12px;
        color: #a5a5a5;
        margin-left: 8px;
      }
    }
  }
  .banner {
    width: 93.6%;
    height: 190px;
    overflow: hidden;
    margin: 0 auto;
    margin-top: 8px;
    border-radius: 7px;
    position: relative;
    img {
      width: 100%;
    }
    .custom-indicator {
      position: absolute;
      right: 16px;
      bottom: 14px;
      font-size: 10px;
      color: #fff;
      span {
        font-size: 17px;
        font-weight: bold;
      }
    }
    .describe {
      width: 100%;
      height: 46px;
      line-height: 46px;
      font-size: 15px;
      padding-left: 15px;
      color: #fff;
      position: absolute;
      left: 0;
      bottom: 0;
      background-image: linear-gradient(
        to bottom,
        rgba(255, 0, 0, 0),
        rgba(0, 0, 0, 0.3)
      );
    }
  }
  .recommendedPosition {
    display: flex;
    margin-top: 18px;
    margin-bottom: 7px;
    .block {
      flex: 1;
      .imgView {
        width: 46px;
        height: 46px;
        border-radius: 50%;
        margin: 0 auto;
        box-shadow: 0 0 11px 0px #bababa;
        position: relative;
        img {
          width: 23px;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;
        }
      }
      p {
        text-align: center;
        font-size: 12px;
        line-height: 12px;
        margin-top: 12px;
        color: #010101;
      }
    }
  }
  .worksView {
    h3 {
      height: 40px;
      line-height: 40px;
      font-size: 16px;
      font-family: "黑体";
      font-weight: bold;
      margin-left: 12px;
    }
    ul {
      width: 93.6%;
      margin: 0 auto;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      li {
        position: relative;
        width: 49.1%;
        border-radius: 5px;
        overflow: hidden;
        margin-bottom: 6px;
        img {
          width: 100%;
        }
        p {
          position: absolute;
          left: 12px;
          top: 12px;
          color: #fff;
          font-size: 12px;
          font-weight: bold;
        }
      }
    }
  }
  .loadingView {
    .loading {
      width: 100%;
      height: 240px;
    }
  }
}
</style>